/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { SET_APPDATA } from 'store/actions';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import ProductFolder from './component/ProductFolder';
import ProductTable from './component/ProductTable';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //
const App = () => {
    console.log('product app running');
    const dispatch = useDispatch();
    const localization = useSelector((state) => state.localization); //read only (language related)
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    const navigate = useNavigate(); //react-router-dom
    const globalization = useSelector((state) => state.globalization);
    const [loginData] = useState(globalization.baseData.loginData);
    let [appData, setAppData] = useState(globalization.appData); //ÂÖ®Â±ÄÊï∞ÊçÆÊöÇÂ≠ò

    //states [ÊâÄÊúâÊñá‰ª∂Â§π Ôºå ÂΩìÂâçÊñá‰ª∂Â§π Ôºå ÂΩìÂâçÈ°µ Ôºå È°µÈù¢Â§ßÂ∞èÔºåÊÄªÊï∞]
    let parentFolderArr = []; //‰∏¥Êó∂Â≠òÊîæ‰∏ãÊñá‰ª∂Â§πidÔºåÂõ†‰∏∫Êï∞ÊçÆ‰∏çËøîÂõûÂêçÂ≠óÔºåËøòÈúÄË¶ÅÈÅçÂéÜ‰∏ã
    let [folderArr, setFolderArr] = useState([]);
    let [currentFolder, setCurrentFolder] = useState(langPack.folder.currentFolder);
    let [page, setPage] = useState(0);
    let [page_size, setPage_size] = useState(10);
    let [total, setTotal] = useState(0);

    //data grid Áõ∏ÂÖ≥
    let [tableColumns, setTableColumns] = useState([]);
    let [tableRows, setTableRows] = useState([]);

    useEffect(() => {
        dispatch({ type: SET_APPDATA, appData });
    }, [dispatch, appData]); //ÂÖ®Â±ÄÊï∞ÊçÆ‰øÆÊîπÔºåÂõ†‰∏∫ÁôªÂΩï

    useEffect(() => {
        if (folderArr.length && tableRows.length) {
            setAppData({ ...globalization.appData, appProduct: { folderArr, tableRows, total, currentFolder } });
        }
        return () => {};
    }, [folderArr, tableRows]); //ËÆ¢ÈòÖÂÖ®Â±ÄÊï∞ÊçÆÂèòÂåñ

    useEffect(() => {
        if (currentFolder && currentFolder !== langPack.folder.currentFolder) {
            getProductList();
            setPage(0);
            tableFormatter();
        }
        return; //Êõ¥Êç¢Êñá‰ª∂Â§πÊó∂
    }, [currentFolder]);

    useEffect(() => {
        setLangPack(Languages[localization.locale]);
        return; //ËØ≠Ë®ÄÂàáÊç¢
    }, [localization.locale]);

    useEffect(() => {
        console.log(tableRows);
        return; //Ë°®Ê†ºÊï∞ÊçÆÂèòÂä®
    }, [tableRows]);

    useEffect(() => {
        return () => {};
    }, [globalization.appData]); //ËÆ¢ÈòÖÂÖ®Â±ÄÊï∞ÊçÆÂèòÂåñ

    useEffect(() => {
        console.log('I want products');
        if (!loginData) return navigate('/');
        let cache = globalization.appData.appProduct;
        if (cache) {
            if (!cache.folderArr || !cache.folderArr.length) getFolders();
            else setFolderArr(cache.folderArr);
            if (!cache.tableRows || !cache.tableRows.length) getProductList();
            else setTableRows(cache.tableRows);
            if (cache.total) setTotal(cache.total);
            setCurrentFolder(cache.currentFolder);
        } //ËØªÂèñÁºìÂ≠ò
        else {
            getFolders();
            getProductList();
        }
        //ÂàùÂßãÂåñ
        tableFormatter();
        return () => {
            console.log('I leave products');
        }; //ËΩΩÂÖ• & Ê∏ÖÁêÜ
    }, []);

    //____________‰∏Ä‰∫õÂáΩÊï∞_______________________________________________

    async function addFolder(obj) {
        let paramObj = ma.productGroupAddQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productGroupAddCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        let { group_id, group_name } = result;
        setFolderArr([...folderArr, { group_id, group_name }]);
    } //Êñ∞Â¢ûÊñá‰ª∂Â§π
    async function getFolders(obj, max) {
        let paramObj = ma.productGroupQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productGroupCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        if (!obj) {
            parentFolderArr = []; //Ê∏ÖÁ©∫‰∏ã
            //Ëé∑Âèñ‰∏ªÁõÆÂΩïÔºåÁÑ∂ÂêéÂºÄÂßãÈÅçÂéÜ
            let childIdArr = result['children_id_list']['number'];
            if (!childIdArr || !childIdArr.length) return;
            childIdArr.forEach((id) => getFolders({ group_id: id }, childIdArr.length));
        } else {
            let { group_id, group_name } = result;
            parentFolderArr.push({ group_id, group_name });
            if (parentFolderArr.length === max) setFolderArr(parentFolderArr);
        }
    } //Ëé∑ÂèñÊñá‰ª∂Â§πÂàóË°®
    async function getProductList(obj) {
        if (!obj) obj = {};
        if (currentFolder && currentFolder !== langPack.folder.currentFolder) {
            obj.group_id1 = currentFolder;
        } //Â≠êÂàÜÁªÑ
        let paramObj = ma.productListQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productListCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        if (tableColumns.find((obj) => obj.field === 'final_score')) tableFormatter();
        if (result.products) {
            let arr = result.products.alibaba_product_brief_response;
            if (!arr || !arr.length) return;
            let rowData = arr.map((obj) => {
                return {
                    ...obj,
                    keywords: obj.keywords ? obj.keywords.string.toString() : '',
                    images: obj.main_image ? obj.main_image.images.string.toString() : ''
                };
            }); //Flatten the data structure to make a easy post
            setTableRows(rowData);
            setTotal(result.total_item);
            return;
        }
        setTableRows([]);
        setTotal(0);
        return;
    } //Ëé∑Âèñ‰∫ßÂìÅÂàóË°®
    async function changeProductDisplay(obj) {
        if (!obj) return false;
        let paramObj = ma.productDisplayQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productDisplayCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        setTimeout(getProductList, 2000);
        return;
    } //‰∫ßÂìÅ‰∏ä‰∏ãÊû∂
    async function checkProductScore() {
        if (!tableRows.length) return;
        let scoreArr = [];
        for (let obj of tableRows) {
            console.log('checking scores');
            let { product_id } = obj;
            let paramObj = ma.productScoreQuery({ product_id });
            let msg = await fetchAPI(paramObj);
            if (!msg.response.status) {
                scoreArr.push({});
                queryResult(false, langPack.steps.productScoreError);
                continue;
            } //ÁΩëÁªúÂºÇÂ∏∏
            let res = ma.productScoreCheck(msg);
            let { status, result } = res.response;
            if (!status) {
                scoreArr.push({});
                queryResult(false, result);
                continue;
            } //Ê≤°Êü•Âà∞
            scoreArr.push(result);
        }
        setTableRows((prev) => {
            let newArr = [];
            for (let i = 0; i < prev.length; i++) {
                newArr.push({ ...prev[i], ...scoreArr[i] });
            }
            return newArr;
        });
        scoreArr = undefined;
        tableFormatter({ score: true });
        return;
    } //‰∫ßÂìÅÂàÜ`ÊâπÈáèÊü•ËØ¢
    async function updateProduct(productArr) {
        if (!productArr || !productArr.length) return;
        for (let obj of productArr) {
            if (!obj) continue;
            let { category_id, keywords, /* main_image, */ product_type, subject, product_id } = obj;
            let newObj = { category_id, keywords, product_type, subject, product_id };
            await updateProductInfo(newObj);
        }
        setTimeout(() => {
            getProductList({ current_page: page + 1, page_size });
        }, 2000); //Êï∞ÊçÆÊõ¥Êñ∞ÂèØËÉΩÊúâÂª∂ËøüÔºåÁºìÂÜ≤‰∏ã
        return;
    } //‰∫ßÂìÅ‰ø°ÊÅØÊõ¥Êñ∞
    async function updateProductInfo(obj) {
        let paramObj = ma.productFieldUpdateQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productFieldUpdateCheck(msg);
        let { status, result } = res.response;
        queryResult(status, result);
        console.log(result);
        return true;
    } //Êõ¥Êñ∞‰∫ßÂìÅ‰ø°ÊÅØ

    //____________ ProductTable functions ___________________________________________________
    function tableFormatter(obj) {
        const columns = [
            {
                field: 'imgWithURL',
                headerName: langPack.table.tableHeader,
                minWidth: 50,
                valueGetter: ({ row }) => row,
                valueFormatter: ({ value }) => value.pc_detail_url,
                renderCell: ({ value }) => (
                    <a href={value.pc_detail_url} target="_blank">
                        <img {...srcset(value.images.split(',')[0], 100, 100, 1, 1)} alt="mainPic" />
                    </a>
                )
            },
            {
                field: 'subject',
                editable: true,
                minWidth: 300,
                headerName: langPack.table.subject
            },
            {
                field: 'keywords',
                editable: true,
                flex: 1,
                headerName: langPack.table.keywords,
                preProcessEditCellProps: (params) => {
                    let { value } = params.props;
                    if (value) {
                        let newArr = value.split(',');
                        if (newArr.length > 3) {
                            newArr.length = 3;
                            queryResult(false, langPack.form.keywords);
                            let keywords = newArr.join(',');
                            return { ...params.props, keywords, error: true };
                        }
                    }
                    return params.props;
                } //‰øÆÊîπÊ†°È™å
            },
            {
                field: 'status',
                flex: 0.5,
                headerName: langPack.table.status
            },
            {
                field: 'display',
                flex: 0.5,
                headerName: langPack.table.display
            },
            {
                field: 'gmt_create',
                flex: 1,
                headerName: langPack.table.gmt_create
            },
            {
                field: 'gmt_modified',
                flex: 1,
                headerName: langPack.table.gmt_modified
            }
        ];
        if (obj) {
            let { score } = obj;
            if (score) {
                columns.splice(
                    5,
                    0,
                    {
                        field: 'final_score',
                        headerName: langPack.table.score,
                        flex: 0.5,
                        sortable: true
                    },
                    {
                        field: 'tag',
                        headerName: langPack.table.tag,
                        flex: 0.5,
                        sortable: true
                    }
                ); // inserts
            }
        }
        setTableColumns(columns);
    }

    const handleChangePage = (newPage) => {
        setPage(newPage);
        getProductList({ current_page: newPage + 1, page_size });
    }; //ÁøªÈ°µ
    const handleChangeRowsPerPage = (newSize) => {
        setPage_size(newSize);
        getProductList({ current_page: page + 1, page_size: newSize });
    }; //È°µÈù¢Â§ßÂ∞èË∞ÉÊï¥
    const onRowClick = (obj, e) => {
        // let { id, row } = obj;
    }; //ÂçïÂÖÉÊ†ºÁÇπÂáª

    //____________JSXÂáΩÊï∞_______________________________________________

    return (
        <MainCard title="Products --‰∫ßÂìÅ">
            <ProductFolder
                folderArr={folderArr}
                currentFolder={currentFolder}
                addFolder={addFolder}
                setCurrentFolder={setCurrentFolder}
                langPack={langPack}
            />
            <ProductTable
                tableRows={tableRows}
                tableColumns={tableColumns}
                page_size={page_size}
                page={page}
                total={total}
                localization={localization}
                langPack={langPack}
                updateProduct={updateProduct}
                checkProductScore={checkProductScore}
                changeProductDisplay={changeProductDisplay}
                handleChangePage={handleChangePage}
                handleChangeRowsPerPage={handleChangeRowsPerPage}
                onRowClick={onRowClick}
            />
        </MainCard>
    );
};

export default App;
