import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';

// material-ui
import { Button } from '@mui/material';
import {
    Speed as SpeedIcon,
    Check as CheckIcon,
    ArrowUpward as ArrowUpwardIcon,
    ArrowDownward as ArrowDownwardIcon,
    /* VisibilityOff as VisibilityOffIcon,
    Construction as ConstructionIcon, */
    ModeEdit as EditIcon
} from '@mui/icons-material';
import {
    DataGrid,
    GridToolbarContainer,
    GridToolbarColumnsButton,
    GridToolbarFilterButton,
    GridToolbarExport,
    GridToolbarDensitySelector,
    zhCN
} from '@mui/x-data-grid';

let selectedArr = []; //ÂΩìÂâçÈÄâ‰∏≠ÁöÑ‰∫ßÂìÅidÔºåÈúÄË¶ÅËΩ¨Êç¢ÊàêÂä†ÂØÜidÊâçÂèØ‰ª•Ë∞ÉÁî®API
let updatedArr = []; //Êõ¥Êñ∞‰∫ÜÁöÑË°å
function ProductTable({
    tableRows,
    tableColumns,
    page_size,
    page,
    total,
    localization,
    langPack,
    updateProduct,
    checkProductScore,
    changeProductDisplay,
    handleChangePage,
    handleChangeRowsPerPage,
    onRowClick
}) {
    const navigate = useNavigate(); //react-router-dom
    //DataGrid toolbar;
    let [checkboxSelection /* , setCheckboxSelection */] = useState(true);
    //let [menuControlText, setMenuControlText] = useState(langPack.tableToolbar.menuHide);
    const tableHeight = window.innerHeight > 520 ? window.innerHeight - 320 : 600;
    const onStateChange = (obj) => {
        let { selection } = obj;
        if (selection.length === selectedArr.length) return;
        selectedArr = selection.map((id) => tableRows.find((obj) => obj.id === id));
        console.log(selectedArr.length + ' selected');
    }; //ÂãæÈÄâ
    const onCellEditCommit = (params) => {
        let { id, row, field, value } = params;
        let oldIndex = tableRows.findIndex((obj) => obj.id === id);
        if (oldIndex === -1) return; //Ê≤°ÊâæÂà∞?
        let oldValue = tableRows[oldIndex][field];
        if (oldValue === value) {
            updatedArr[oldIndex] = undefined;
            return;
        } //ÂÖàÂØπÊØîÂéüÂßãÊï∞ÊçÆÔºåÂ¶ÇÊûúÊ≤°ÊúâÊîπÂèòÔºåÂàô‰∏çÊõ¥Êñ∞
        let newData = { ...row, [field]: value };
        updatedArr[oldIndex] = newData; //‰∏çÁÆ°Êîπ‰∏çÊîπÔºåÂÖàÂ≠òÁùÄÂÜçËØ¥
        return;
    };
    return (
        <div style={{ height: tableHeight, width: '100%' }}>
            <DataGrid
                experimentalFeatures={{ newEditingApi: true }}
                checkboxSelection={checkboxSelection}
                localeText={localization.locale === 'zhCN' ? zhCN.components.MuiDataGrid.defaultProps.localeText : {}}
                components={{
                    Toolbar: () => (
                        <GridToolbarContainer>
                            <GridToolbarColumnsButton />
                            <GridToolbarFilterButton />
                            <GridToolbarDensitySelector />
                            <GridToolbarExport
                                csvOptions={{
                                    utf8WithBom: true,
                                    fileName:
                                        langPack.table.tableHeader +
                                        '_' +
                                        new Date(new Date().valueOf() - new Date().getTimezoneOffset() * 60 * 1000)
                                            .toISOString()
                                            .replace(/T.*/, '')
                                }}
                            />
                            {/* <Button
                                    sx={{ padding: '4px 5px' }}
                                    startIcon={
                                        menuControlText === langPack.tableToolbar.menuHide ? <VisibilityOffIcon /> : <ConstructionIcon />
                                    }
                                    onClick={() => {
                                        setCheckboxSelection(!checkboxSelection);
                                        setMenuControlText(
                                            menuControlText === langPack.tableToolbar.menuHide
                                                ? langPack.tableToolbar.menuShow
                                                : langPack.tableToolbar.menuHide
                                        );
                                    }}
                                >
                                    {menuControlText}
                                </Button> */}
                            <Button sx={{ padding: '4px 5px' }} startIcon={<SpeedIcon />} onClick={checkProductScore}>
                                {langPack.tableToolbar.productScore}
                            </Button>
                            <span hidden={!selectedArr.length}>
                                <Button
                                    sx={{ padding: '4px 5px' }}
                                    startIcon={<ArrowUpwardIcon />}
                                    onClick={() => {
                                        let list = selectedArr.map((obj) => obj.product_id).join(',');
                                        changeProductDisplay({
                                            new_display: 'on',
                                            product_id_list: list
                                        });
                                    }}
                                >
                                    {langPack.tableToolbar.productDisplayOn}
                                </Button>
                                <Button
                                    sx={{ padding: '4px 5px' }}
                                    startIcon={<ArrowDownwardIcon />}
                                    onClick={() => {
                                        let list = selectedArr.map((obj) => obj.product_id).join(',');
                                        changeProductDisplay({
                                            new_display: 'off',
                                            product_id_list: list
                                        });
                                    }}
                                >
                                    {langPack.tableToolbar.productDisplayOff}
                                </Button>
                                <Button
                                    sx={{ padding: '4px 5px' }}
                                    startIcon={<EditIcon />}
                                    disabled={selectedArr.length !== 1}
                                    onClick={() => {
                                        if (!selectedArr.length) return;
                                        let product_id = selectedArr.map((obj) => obj.product_id).join(',');
                                        navigate(`/product/${product_id}`);
                                        return;
                                    }}
                                >
                                    {langPack.tableToolbar.productEdit}
                                </Button>
                            </span>
                            <span hidden={!updatedArr.length}>
                                <Button
                                    color="error"
                                    sx={{ padding: '4px 5px' }}
                                    startIcon={<CheckIcon />}
                                    onClick={async () => {
                                        await updateProduct(updatedArr);
                                        updatedArr = [];
                                    }}
                                >
                                    {langPack.tableToolbar.productUpdate}
                                </Button>
                            </span>
                        </GridToolbarContainer>
                    )
                }}
                onRowClick={onRowClick}
                onCellEditCommit={onCellEditCommit}
                onStateChange={onStateChange}
                page={page}
                pageSize={page_size}
                rowCount={total}
                columns={tableColumns}
                getRowHeight={() => 88}
                rows={tableRows}
                rowsPerPageOptions={[10, 25, 100]}
                disableSelectionOnClick
                paginationMode="server"
                onPageChange={handleChangePage}
                onPageSizeChange={handleChangeRowsPerPage}
            />
        </div>
    );
}

ProductTable.propTypes = {
    tableRows: PropTypes.array,
    tableColumns: PropTypes.array,
    page_size: PropTypes.number,
    page: PropTypes.number,
    total: PropTypes.number,
    localization: PropTypes.object,
    langPack: PropTypes.object,
    updateProduct: PropTypes.func,
    changeProductDisplay: PropTypes.func,
    checkProductScore: PropTypes.func,
    handleChangePage: PropTypes.func,
    handleChangeRowsPerPage: PropTypes.func,
    onRowClick: PropTypes.func
};

export default ProductTable;
